.ball {
  list-style: none;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background-color: #fff;
  transform: translateY(18px);
}

.ball:nth-child(1) {
  animation: bounce 0.7s ease-in-out infinite;
}

.ball:nth-child(2) {
  animation: bounce 0.7s ease-in-out 0.2s infinite;
}

.ball:nth-child(3) {
  animation: bounce 0.7s ease-in-out 0.4s infinite;
}

@keyframes bounce {
  50% {
    transform: translateY(14px);
  }
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track {
  background: #ffffff;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #bebebe;
  -webkit-box-shadow: 0 0 1px #70c58b;
}
